	import uniLoadMore from "@/components/uni-load-more.vue"
	export default {
		components: {
			uniLoadMore
		},
		data() {
			return {
				tabBars: '', //知识分类
				scrollLeft: 0,
				tabIndex: 0,
				authKey:'',
				symbolId:'',
				lists:'',
				loadingType: 0,
				next : -1,
				start: 0,
				size: 15,
				record: false,
				bottom: false,
				give:'',
				token:''
			};
		},  
		onLoad: function(e) {
			this.authKey = uni.getStorageSync('authKey');
			// console.log(this.authKey)
			this.requestLearn()
		},
		onShow() {
			this.start = 0
			
		},
		onReachBottom(){
			if (this.loadingType == 2) {
				return
			}
			this.requestLearnList(false)
		},
		methods: {
			getElSize(id) { //得到元素的size
			console.log(id)
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data)
					}).exec()
				})
			},
			async tapTab(e) { //点击tab-bar
				this.symbolId = e.currentTarget.id
				console.log(this.symbolId)
				if (this.tabIndex === e.target.dataset.current) {
					return false;
				} else {
					let tabBar = await this.getElSize("tab-bar"),
						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = e.target.dataset.current;
				}
				this.start = 0
				this.requestLearnList()
			},
			// 请求知识分类
			requestLearn: function() {
				var that = this;
				uni.request({
					url: this.http+'knowledge/category',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							that.tabBars = res.data.data
							that.symbolId = res.data.data[0].id;
							this.requestLearnList()
						}
					},
				});
			},
			// 请求知识分类的列表
			requestLearnList: function(isLoading = true) {
				if (isLoading)	uni.showLoading({title: '加载中'})
				var that = this;
				that.loadingType = 1
				let querys = "?start=" + this.start + "&size=" + this.size 
				uni.request({
					url: this.http+'collection/list' + querys,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
						category_id:that.symbolId
					},
					success: res => {
						let data = res.data.data
						let that = this;
						if (res.data.code == 200) {
							// that.lists = res.data.data.list
							let order = data.list
							order.forEach(function(item){
								item.cover_src = that.static + item.cover_src
							})
							
							if (that.start == 0) 
								that.lists = []
                           
							that.lists = that.lists.concat(order)
							that.start = data.next	
							if (order.length < that.size || that.start == -1)
								that.loadingType = 2
							else 
								that.loadingType = 0
							if (that.lists.length == 0) 
								that.record = true
							else 
								that.record = false
							 
						}
					},
					fail: () => {
						console.log("请求失败")
					},
					complete: () => {
						if(isLoading) uni.hideLoading()
					}
				});
			},
			// 取消收藏
			cancel(index){
				var that = this;
				uni.request({
					url: this.http+'collection/remove/' + this.lists[index].id,
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {
						
					},
					success: res => {
						console.log(res.data)
						if (res.data.code == 200) {
							uni.showToast({
								title: '取消收藏成功',
								icon:'none',
								mask: false,
								duration: 1500
							});
							this.lists.splice(index, 1)
						}
					},
				});
			},
			// 取消收藏
// 			cancel(id){
// 				var that = this;
// 				uni.request({
// 					url: this.http+'collection/remove/' + id,
// 					method: 'GET',
// 					header: {
// 						authKey: this.authKey
// 					},
// 					data: {
// 						
// 					},
// 					success: res => {
// 						console.log(res.data)
// 						if (res.data.code == 200) {
// 							uni.showToast({
// 								title: '取消收藏成功',
// 								icon:'none',
// 								mask: false,
// 								duration: 1500
// 							});
// 							that.start = 0
// 							that.requestLearnList()
// 						}
// 					},
// 				});
// 			},
			goDetail(id){
				uni.navigateTo({
					url: '/pages/knowledge/detail?id=' + id
				});
			},
			lower(){
				if (this.loadingType == 2) {
					return
				}
				this.requestLearnList(false)
			}
		}
	}
